import React, { useState } from 'react';
import { Moralis } from 'moralis';
import WalletConnect from '@walletconnect/client';
import './App.css'; // Import your CSS file

// Initialize Moralis
Moralis.start({ apiKey: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJub25jZSI6ImIwY2QyNmNhLTk3YzItNDkyZC05NTY1LTgyNjNhODA0MmYzMiIsIm9yZ0lkIjoiNDA4ODc2IiwidXNlcklkIjoiNDIwMTQ5IiwidHlwZUlkIjoiMWM5YjRlYWEtOWFiMC00YzJiLTk3NzItZTNiNTZmYTcwZjEzIiwidHlwZSI6IlBST0pFQ1QiLCJpYXQiOjE3MjY3ODExNDksImV4cCI6NDg4MjU0MTE0OX0.KYnV7lzIy2YIMESQkVykME-3f8QbVDoDPWopbQcZGUI' });

const App = () => {
    const [walletAddress, setWalletAddress] = useState(null);
    const [loading, setLoading] = useState(false);

    const connectWallet = async () => {
        setLoading(true);
        const connector = new WalletConnect({
            bridge: "https://bridge.walletconnect.org",
        });

        if (!connector.connected) {
            await connector.createSession();
        }

        const uri = connector.uri;
        console.log('QR Code URI:', uri);

        connector.on("connect", async (error, payload) => {
            if (error) {
                console.error(error);
                setLoading(false);
                return;
            }

            const { accounts } = payload.params[0];
            const user = await Moralis.authenticate({ provider: "walletconnect" });
            setWalletAddress(accounts[0]);
            console.log("User:", user);
            console.log("Connected address:", accounts[0]);
            setLoading(false);
        });
    };

    return (
        <div className="app-container">
            <h1>Connect Your Wallet</h1>
            <button className="connect-button" onClick={connectWallet} disabled={loading}>
                {loading ? "Connecting..." : "Connect Wallet"}
            </button>
            {walletAddress && <p className="address">Connected: {walletAddress}</p>}
        </div>
    );
};

export default App;